.newsContent {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 75%;
  min-height: 70vh;
  margin: 4vh auto;
  border-radius: 0.5rem;
  padding: 0.3rem;
  text-align: justify;
  word-break: break-word;

  h1 {
    font-size: 2rem;
    margin: 0 0 0 1rem;
  }

  h2 {
    font-size: 1.5rem;
    margin: 0 1rem 0.5rem;
  }

  .newsSection {
    display: flex;
    flex-direction: column;

    .sectionContent {

      .imgContainer {
        max-width: 25rem;
        max-height: 25rem;

        img {
          float: left;
          width: 100%;
          margin: 0 1rem 0.5rem 1.5rem;
        }
      }

      p {
        margin: 0 1.5rem;
      }

      .highlighted {
        text-decoration: underline;
        text-decoration-thickness: 0.2rem;
      }
    }
  }

  .likeButtonContainer {
    display: flex;
    justify-content: space-between;
    margin: 1.5rem 1.6rem 0;

    h4 {
      display: flex;
    }

    .likeButton {
      background-color: transparent;
      border: none;
      margin: -1rem 0 1rem 1.5rem;
      cursor: pointer;
      font-size: 2rem;
      transition: color 0.3s ease, transform 0.2s ease;
      outline: none;
      display: flex;
      align-items: center;
      justify-content: center;

      &.liked {
        color: #ff0000;
      }

      &:hover:not(.liked) {
        transform: scale(1.2);
      }
    }
  }
}