.search-container {
    padding: 1rem;
    padding-top: 0;
    display: flex;
    flex-direction: column;
    width: 100%;

    .grid {
        margin: 1rem;
        display: grid;
        gap: 1rem;
    }

    hr {
        margin-top: 0.5rem;
    }

    .header {
        display: flex;
        align-items: center;
        max-width: 100%;
        margin: 0 1.5rem;

        .filter-wrapper {
            position: relative;
            display: inline-block;

            .burger-filter {
                position: absolute;
                top: 100%;
                left: 0;
                background: none;
                padding: 0.1rem;
                display: none;
                z-index: 1;
            }

            .burger-filter.show {
                display: block;
            }
        }


        .controls {
            display: flex;
            gap: 0.5rem;
            justify-self: right;
            margin-left: auto;
        }
    }

    button {
        background-color: $main-clr;
        border: 0 solid #E5E7EB;
        box-sizing: border-box;
        color: #000000;
        display: flex;
        font-weight: 700;
        justify-content: center;
        line-height: 1.75rem;
        padding: .75rem .75rem;
        position: relative;
        text-align: center;
        text-decoration: none #000000 solid;
        text-decoration-thickness: auto;
        width: fit-content;
        position: relative;
        cursor: pointer;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;

        &:after {
            content: '';
            position: absolute;
            border: 1px solid #000000;
            bottom: 2px;
            left: -2px;
            width: calc(100% - 1px);
            height: calc(100% - 1px);
        }

        &:hover::after {
            bottom: 0;
            left: 0;
        }

        &:active {
            background-color: $main-clr-shadow;
        }

        &.selected {
            background-color: $main-clr-shadow;
        }

        &.selected::after {
            bottom: 0;
            left: 0;
        }
    }
}